/* eslint-disable */
import { v4 as uuidv4 } from 'uuid';
import AsyncStorage from '@react-native-async-storage/async-storage';

const KEY_LAST_ACTIVE = 'aa_session_last_active';
const KEY_SESSION_ID = 'aa_session_id';
const SESSION_INACTIVITY_MILISECONDS = 60000; // 30 minutes

class AASession {
  constructor() {
    this.update();
  }
  async persistData(key, value) {
    await AsyncStorage.setItem(key, value);
  }
  async getData(key) {
    return await AsyncStorage.getItem(key);
  }
  async generateSessionId() {
    const sessionId = uuidv4();
    await this.persistData(KEY_SESSION_ID, sessionId);
  }
  async getSessionId() {
    return await this.getData(KEY_SESSION_ID);
  }
  async setLastActive(timestamp) {
    await this.persistData(KEY_LAST_ACTIVE, timestamp);
  }
  async getLastActive() {
    return await this.getData(KEY_LAST_ACTIVE);
  }
  async update() {
    const currentTimestamp = Date.now();
    const lastActive = await this.getLastActive();
    if (lastActive) {
      const lastActiveNumber = parseInt(lastActive, 10);
      if (currentTimestamp > lastActiveNumber + SESSION_INACTIVITY_MILISECONDS) {
        await this.generateSessionId();
      }
      this.setLastActive(currentTimestamp);
    } else {
      // Initial page load for new user
      await this.generateSessionId();
      await this.setLastActive(currentTimestamp);
    }
  }
}

export default AASession;